body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-lg: 18px;
  --font-size-13xl: 32px;
  --font-size-xl: 20px;
  --font-size-6xl: 25px;

  /* Colors */
  --color-black: #000;
  --color-salmon: #ff7676;
  --color-lightskyblue: #87beff;
  --color-mediumseagreen: #76c074;
  --color-white: #fff;
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-5xl: 24px;

  /* Paddings */
  --padding-11xs: 2px;
  --padding-4xs: 9px;
  --padding-xs: 12px;
  --padding-44xl: 63px;
  --padding-39xl: 58px;

  /* border radiuses */
  --br-8xs: 5px;
}
